.table {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    width: 800px;
    height: 400px;
    border-radius: 300px;
    background-color: green;
    box-shadow:
        0 0 0 2px black,
        0 0 0 30px brown,
        0 0 0 32px black;
}

.input {
    position: absolute;
    bottom: 40px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.card {
    width: 70px;
    margin-left: 50px;
}

.card-selected {
    border: red solid 2px;
}

.own-card {
    position: absolute;
}

.top-half {
    clip: rect(0px, 70px, 50px, 0px)
}

.bottom-half {
    height: 100px;
    clip: rect(50px, 70px, 100px, 0px)
}
